* {
  box-sizing: border-box;
}

html, body {min-height: 100%;}

$dark-blue: #707AB4;
$light-blue: #C4D0F0;
$light: #f0f4ff;
$pink: #ECC1F3;
$font: 'Open Sans', sans-serif;


@mixin center {
  margin-left: auto;
  margin-right: auto;
}

@mixin radius {
  border-radius: 10px;
}

@mixin flex-row {
  display: flex;
  flex-direction: row;
}

@mixin shadow {
  box-shadow: 5px 5px 15px rgba(157, 170, 209, 0.5);
}

@mixin hidden {
  display: none;
}

body {
  background: linear-gradient(120deg, #8690EC 0%, #CFE2F9 27.6%, #FFFFFF 50%, #CEC1F3 72.92%, #747FE8 100%) no-repeat;
  background-size: cover;
  font-family: $font;
  padding-bottom: 50px;
}

h1, h2, h3, p {
  margin: 0;
}

h1 {
  font-style: normal;
  font-weight: 300;
  font-size: 30px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

h2 {
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
}

h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  text-transform: capitalize;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  display: block;
  background-color: $light;
  color: $dark-blue;
  @include radius;
  @include shadow;
  padding: 18px 22px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  border: none;
  cursor: pointer;

  &.random {
    @include center;
    margin-top: 112px;
  }

  &:hover {
    background-color: white;
  }

}

.container {
  max-width: 1140px;
  @include center;
}

.card {
  width: 600px;
  background: #FFFFFF;
  padding: 50px;
  margin-top: 38px;
  @include radius;
  @include center;
  @include shadow;
  color: $dark-blue;

  .card__header {
    @include flex-row;
    align-items: center;
  }

  .card__image {
    @include radius;
    border: 1px solid $light-blue;
    width: 100px;
    height: 100px;
    background: no-repeat center;
    margin-right: 25px;
  }

  h1 {
    margin-right: auto;
  }

  h2 {
    margin-top: 45px;
  }

  .ability {
    h3 {
      margin-top: 8px;
      margin-left: 34px;
      cursor: pointer;

      .ability__dropdown {
        stroke: $dark-blue;
        margin-left: 9px;
        margin-bottom: 1px;
      }
    }

    .ability__description {
      margin-top: 8px;
      margin-left: 68px;
      margin-right: 68px;

      &::after {
        content: '';
        display: block;
        height: 1px;
        width: 100%;
        background-color: $light-blue;
        margin-top: 12px;;
      }

      &.collapse {
        @include hidden;
      }
    }
  }
}

.like {
  margin-left: auto;
  align-self: flex-start;
  stroke: $light-blue;
  cursor: pointer;

  &.active {
    fill: $pink;
    stroke: $pink;
  } 
}

.lds-ellipsis {
  @include center;
  div {
    background: $dark-blue !important;
  }
}